<template>
    <div class="loginSection">
        <section class="loginContent">
            <h2 class="loginTitle">
                piper后台管理系统
            </h2>
            <div class="item">
                <span>账号：</span>
                <el-input v-model="userId" placeholder="请输入账号" @keyup.enter.native="loginSubmit"></el-input>
            </div>
            <div class="item">
                <span>密码：</span>
                <el-input v-model="userPwd" placeholder="请输入密码" show-password  @keyup.enter.native="loginSubmit"></el-input>
            </div>
            <div class="loginBtn">
                <el-button type="primary" @click="loginSubmit">登录</el-button>
            </div>
        </section>
    </div>
</template>

<script>
import { mapActions } from 'vuex';
export default {
    data() {
        return {
            userId: '', // 用户账号
            userPwd: '', // 用户密码
        }
    },
    methods: {
        ...mapActions({
            getUserInfo: 'getUserInfo'
        }),
        // 登录
        loginSubmit () {
            if (this.userId === '') {
                this.$message({
                    message: '请输入账号',
                    type: 'warning'
                });
                return;
            }
            if (this.userPwd === '') {
                this.$message({
                    message: '请输入密码',
                    type: 'warning'
                });
                return;

            }
            let obj = {
                loginName: this.userId,
                password: this.userPwd
            }
            this.getUserInfo(obj).then(() => {
                this.$router.push('/home');
            });
        }
    }
}
</script>

